import react from 'react'
import axios from 'axios'
import Router from 'next/router'


class Home extends React.Component {
    constructor(props){
        super(props)

    }
    componentDidMount(){
        axios.get("http://localhost/TEST/syslogin.php")

        
    }
    Logout(){
        Router.push('/')

    }
    render(){
        return(
            <div>
                <h1>
                    Login Successfully
                    <br />
                    <button onClick={this.Logout.bind(this)}>Logout</button>
                
                </h1>
            </div>
        )
    }
}

export default Home